<template><undefined>

<h1>Card 卡片</h1>
<p>通用卡片容器。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>最基础的卡片容器，可承载文字、列表、图片、段落，常用于后台概览页面。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;  \n    &lt;v-card title=\&quot;Card title\&quot; @mouseleave.native=\&quot;changeShow\&quot; @mouseenter.native=\&quot;changeShow\&quot;&gt;\n        &lt;p&gt;Card content&lt;/p&gt;\n        &lt;p&gt;Card content&lt;/p&gt;\n        &lt;p&gt;Card content&lt;/p&gt;\n        &lt;div slot=\&quot;extra\&quot; v-show=\&quot;isShow\&quot;&gt;\n            &lt;v-button type=\&quot;primary\&quot;&gt;编辑&lt;/v-button&gt;\n            &lt;v-button type=\&quot;danger\&quot;&gt;删除&lt;/v-button&gt;\n        &lt;/div&gt;\n    &lt;/v-card&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: function() {\n            return {\n                isShow:false\n            }\n        },\n        methods: {\n            changeShow(){\n                this.isShow = !this.isShow;\n            },\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>  
    <v-card title="Card title" @mouseleave.native="changeShow" @mouseenter.native="changeShow">
        <p>Card content</p>
        <p>Card content</p>
        <p>Card content</p>
        <div slot="extra" v-show="isShow">
            <v-button type="primary">编辑</v-button>
            <v-button type="danger">删除</v-button>
        </div>
    </v-card>
</template>

</div>
