<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="card-props"><a class="header-anchor" href="#card-props" aria-hidden="true">¶</a> Card Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>卡片标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>slot:extra</td>
<td>卡片右上角的操作区域</td>
<td>Slot Node</td>
<td>-</td>
</tr>
<tr>
<td>bordered</td>
<td>是否有边框</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>bodyStyle</td>
<td>内容区域自定义样式</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>noHovering</td>
<td>取消鼠标移过浮起</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>loading</td>
<td>当卡片内容还在加载中时，可以用 loading 展示一个占位</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
</undefined></template>
<style>
    .demo-box.demo-alert .el-alert {
        margin: 20px 0 0;
