        margin: 0;
    }
</style>
<script>
    export default {
        data: function() {
            return {
                loading: true,
                isShow:false
            }
        },
        methods: {
            onClick() {
                this.loading = !this.loading;
            },
            changeShow(){
                this.isShow = !this.isShow;
            },
        }
    }
</script>


